.rsvp-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 3/4;
		padding-top: 1rem;
		.rsvp-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 500px 500px 0 0;
		}
	}
	.rsvp-wrapper {
		position: relative;
		padding: 3rem 0;
		background: linear-gradient(180deg, var(--color-primary), transparent);
		border-bottom-right-radius: var(--border-radius);
		border-bottom-left-radius: var(--border-radius);
		box-shadow: 0 10px 1rem rgba($color: #000000, $alpha: 0.05);
		@include max-lg {
			padding: 1rem 0;
		}
		.title-section {
			h2 {
				color: var(--color-dark);
			}
		}
		.rsvp-form {
			padding: 2rem;
			position: relative;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}
}
