.couple-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		color: var(--color-dark);
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		z-index: 3;
		span {
			position: relative;
			font-size: 24px;
			font-family: $font-secondary;
		}
	}
	.couple-content {
		position: relative;
		margin-bottom: 2rem;
	}
	.couple {
		position: relative;
		text-align: center;
		.image-wrap {
			width: 80%;
			position: relative;
			aspect-ratio: 3/4;
			height: auto;
			margin: 0 auto;
			border-radius: 1000px 1000px 0 0;
			padding-top: 2rem;
			.couple-image {
				height: 100%;
				object-fit: cover;
				position: relative;
				width: 100%;
				border-radius: 1000px 1000px 0 0;
			}
		}
		.couple-description {
			position: relative;
			padding: 2rem 1rem;
			h3 {
				color: var(--color-secondary);
			}
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: 100%;
			.sosmed {
				color: var(--color-dark);
				text-decoration: none;
				display: block;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
	.reminder-wrap {
		padding: 3rem 0 5rem;
		@include max-md {
			padding: 3rem 0;
		}
		.elements-wrapper-absolute {
			height: calc(100% + 5rem);
		}
	}
}
