.stream-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.stream-wrapper {
		position: relative;
		border-radius: var(--border-radius);
		padding: 2rem;
		z-index: 5;
		@include max-md {
			padding: 2rem 1rem;
		}
		.stream-content {
			position: relative;
			&::before {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				border-radius: 500px 500px 0 0;
				background-color: var(--color-light);
				content: "";
				opacity: 0.6;
			}
			.stream-description {
				position: relative;
				padding: 8rem 2rem 3rem;
				@include max-md {
					padding: 5rem 1rem 2rem;
				}
			}
			h4 {
				@include max-md {
					text-align: center;
				}
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.card {
		background-color: transparent;
		height: 100%;
		color: var(--color-dark);
		border: none;
	}
	.btn-custom {
		width: 100%;
		z-index: 10;
		position: relative;
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		width: 100%;
		margin-top: 3rem;
		@include max-md {
			text-align: center;
		}
	}
}
