.story-section {
	position: relative;
	padding: 0 0 5rem;
	@include max-md {
		padding: 0 0 3rem;
	}
	.orn-1 {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 160%;
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		text-align: center;
		padding: 3rem 1rem;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		aspect-ratio: 16/9;
		height: auto;
		@include max-md {
			aspect-ratio: 4/3;
		}
		.story-background {
			position: relative;
			object-fit: cover;
			width: 100%;
			height: 100%;
		}
	}
}
