.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	height: 100vh;
	width: 100%;
	.elements-wrapper-absolute {
		.background-element {
			img {
				object-position: 50% 15%;
				@include max-md {
					object-position: 50% 0%;
				}
			}
		}
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100%;
	}
	.orn-2 {
		position: absolute;
		top: 0;
		left: 0;
		width: 160px;
		transform: translate(-30%, -10%);
		@include max-md {
			width: 130px;
		}
		@include max-sm {
			width: 100px;
		}
		.image-element.img-2 {
			opacity: 0.5;
		}
		.orn-3 {
			width: 200px;
			transform: translate(-15%, -30%);
			@include max-md {
				width: 160px;
			}
			@include max-sm {
				width: 140px;
			}
		}
	}
	.orn-4 {
		position: absolute;
		top: 0;
		right: 0;
		width: 160px;
		transform: translate(30%, -10%) scaleX(-1);
		@include max-md {
			width: 130px;
		}
		@include max-sm {
			width: 100px;
		}
		.orn-5 {
			width: 180px;
			transform: translateY(-30%);
			@include max-md {
				width: 140px;
			}
			@include max-sm {
				width: 120px;
			}
		}
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		@include max-lg {
			padding: 4rem 0 8rem;
			align-items: center;
		}
		.cover-content {
			text-align: center;
			position: relative;
			background-color: transparent;
			border-radius: 12px;
			color: var(--color-dark);
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			@include max-mhc(667px) {
				align-items: flex-start;
			}
			.cover-body {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				@include max-lg {
					flex-direction: column;
				}
				.greeting-wrapper {
					position: relative;
					@include max-md {
						font-size: 14px;
					}
					h6 {
						font-weight: 700;
					}
				}
				h6 {
					font-weight: 600;
				}
				.image-wrapper {
					position: relative;
					width: 220px;
					height: auto;
					aspect-ratio: 3/4;
					padding-top: 0.5rem;
					@include max-lg {
						width: 50%;
						margin: 0 auto;
					}
					@include max-md {
						width: 70%;
						height: 100%;
					}
					@include max-xs {
						width: 60%;
					}
					.cover-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
						border-radius: 500px 500px 10px 10px;
						position: relative;
					}
				}
				.cover-description {
					padding: 0 2rem;
					text-align: center;
					@include max-lg {
						padding: 2rem 0 1rem;
					}
					* {
						position: relative;
					}
				}
			}
			.elements-widget {
				width: 100%;
			}
		}
	}
}
