.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		bottom: 5rem;
		right: 0;
		transform: translateX(60%);
		width: 250px;
	}
	.appology-wrap {
		position: relative;
		padding: 8rem 2rem 3rem;
		background: var(--color-main);
		border-radius: 500px 500px 30px 30px;
		@include max-md {
			padding: 5rem 1rem 2rem;
		}
		.background-element {
			img {
				object-fit: cover;
				object-position: 50% 100%;
				border-radius: 500px 500px 30px 30px;
			}
		}
	}
}
